@font-face {
  font-family: "OswaldRegular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("..//..//fonts//oswaldregular.woff2") format("woff2"), url("..//..//fonts//oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "OswaldMedium";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("..//..//fonts//oswaldmedium.woff2") format("woff2"), url("..//..//fonts//oswaldmedium.woff") format("woff");
}
.page-header {
  width: 100%;
  z-index: 2;
}

.page-body {
  margin: 0 auto;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  .page-body {
    font-size: 16px;
    line-height: 24px;
    align-items: stretch;
  }
}
.main-nav {
  font-family: "OswaldMedium", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  position: relative;
  display: flex;
  justify-content: space-between;
}

.main-nav__logo {
  padding: 10px 20px 15px 20px;
}

.main-nav__logo-text {
  display: block;
}

.main-nav__logo-text--position {
  padding-top: 8px;
}

.main-nav__toggle {
  border: none;
  padding: 0;
  background-color: transparent;
  width: 24px;
  height: 18px;
  background-repeat: no-repeat;
  background-position: center;
}

.main-nav__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  position: absolute;
  top: 65px;
  left: 0;
  z-index: 1;
  background-color: #ffffff;
}

.main-nav--closed {
  padding: 10px 20px 15px 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: auto;
}
@media (min-width: 768px) {
  .main-nav--closed {
    display: grid;
    grid-template-columns: auto 1fr;
  }
}

.main-nav--closed .main-nav__logo {
  padding: 0;
}

.main-nav--closed .main-nav__list {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: grid;
  }
}

.main-nav--closed .main-nav__toggle {
  display: block;
  background-image: url("../img/open-menu.svg");
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}

.main-nav--opened {
  align-items: center;
  width: auto;
  padding: 10px 20px 17px 20px;
}

.main-nav--opened .main-nav__logo {
  padding: 0;
}

.main-nav--opened .main-nav__toggle {
  display: block;
  background-image: url("../img/close-menu.svg");
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__toggle {
    display: none;
  }
}

.main-nav--opened .main-nav__list {
  display: flex;
}

.main-nav__item {
  list-style-type: none;
  padding-top: 22px;
  padding-bottom: 24px;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  width: 100%;
  text-align: center;
}

.main-nav__link {
  text-decoration: none;
  color: #000000;
}

.main-nav__link:hover {
  opacity: 0.6;
}

.main-nav__link:active {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .main-nav--nojs {
    flex-direction: column;
    width: 100%;
    padding: 0;
    align-items: stretch;
  }

  .main-nav--nojs .main-nav__logo {
    padding: 10px 20px 17px 20px;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__list {
    display: flex;
    flex-direction: column;
    position: static;
  }
}
@media (min-width: 768px) {
  .main-nav {
    font-family: "OswaldRegular", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 24px;
  }

  .main-nav__logo {
    margin-right: 93px;
  }

  .main-nav__logo-text {
    display: none;
  }

  .main-nav__list {
    position: static;
    display: grid;
    grid-auto-flow: column;
  }

  .main-nav__item {
    border: none;
    padding: 0;
    padding-top: 18px;
    text-align: left;
  }
  .main-nav__item:first-of-type {
    margin-right: 34px;
  }
  .main-nav__item:nth-of-type(2) {
    margin-right: 36px;
  }

  .main-nav__link {
    display: block;
    width: fit-content;
    padding-bottom: 5px;
  }

  .main-nav__link--current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    font-size: 20px;
    line-height: 30px;
    padding: 53px 110px 30px 110px;
    grid-template-columns: 1fr auto;
  }

  .main-nav__list {
    background-color: transparent;
  }

  .main-nav__item {
    padding-top: 10px;
  }

  .main-nav__item--main-page .main-nav__link {
    color: #ffffff;
  }

  .main-nav__item--main-page .main-nav__link--current {
    border-bottom: 2px solid #ffffff;
  }

  .main-nav__link {
    padding-bottom: 8px;
  }
}
