  margin: 0 12px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.slider__toggler {
  display: flex;
  align-items: center;
  padding: 0 5px;
  width: 85px;
  height: 14px;
  border-radius: 6px;
  background-color: #ffffff;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
}
.slider__toggler::before {
  content: "";
  display: block;
  position: absolute;
  width: 36px;
  height: 6px;
  border-radius: 6px;
  background-color: #68B738;
